import { Query } from 'react-apollo';
import Error from './ErrorMessage';
import gql from 'graphql-tag';
import PropTypes from 'prop-types';

import Table from './styles/Table'
import SickButton from './styles/SickButton';

const possiblePermissions = [
    'ADMIN',
    'USER',
    'ITEMCREATE',
    'ITEMUPDATE',
    'ITEMDELETE',
    'PERMISSIONUPDATE'
]

const ALL_USERS_QUERY = gql`
    query ALL_USERS_QUERY {
        users {
            id
            name
            email
            permissions
        }
    }
`

const Permissions = () => {
    return (
        <Query query={ALL_USERS_QUERY}>
            {
                ({ data, loading, error }) => (
                    <>
                        <Error error={error} />
                        <div>
                            <h2>Manage Permissions</h2>
                            <Table>
                                <thead>
                                    <tr>
                                        <th>Name</th>
                                        <th>Email</th>
                                        {possiblePermissions.map((permission, index) => <th key={index}>{permission}</th>)}
                                        <th></th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {data.users.map(user => <UserPermissions key={user.id} user={user} />)}
                                </tbody>
                            </Table>
                        </div>
                    </>
                )
            }
        </Query>
    )
}

import React, { Component } from 'react'

class UserPermissions extends Component {
    static propTypes = {
        user: PropTypes.shape({
            name: PropTypes.string,
            email: PropTypes.string,
            id: PropTypes.string,
            permissions: PropTypes.array
        }).isRequired
    }

    state = {
        permissions: this.props.user.permissions
    }

    handlePermissionChange = e => {
        const {target: {value, checked}} = e;
        const updatedPermissions = this.state.permissions;
        if(checked){
            updatedPermissions.push(value)
        }
    }

    render() {
        const {user} = this.props;
        const {permissions} = this.state;
        return (
           <tr>
                <td>{user.name}</td>
                <td>{user.email}</td>
                {possiblePermissions.map((permission, index) => (
                    <td key={index}>
                        <label htmlFor={`${user.id}-permission-${permission}`}>
                            <input type="checkbox" onChange={this.handlePermissionChange} checked={permissions.includes(permission)} name="" id=""/>
                        </label>
                    </td>
                ))}
                <td>
                    <SickButton>Update</SickButton>
                </td>
           </tr>
        )
    }
}

export default Permissions
